.form__labelText {
    width: 130px;
    display: inline-block;
}

.form__field {
    border: 1px solid #3a3a3a;
    padding: 5px;
    max-width: 250px;
    width: 100%;
    border-radius: 5px;
}

.form__legend {
    padding: 5px;
    border-radius: 5px;
    border: none;
    color: #fff;
    background-color: #b8860b;
    box-shadow: 0px 0px 13px 1px #333;

}

.form__fieldset {
    border: 1px solid #3a3a3a;
    background-color: rgba(231, 231, 231, 0.486);
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0px 0px 19px 1px #1b1a1a;
}

.form {
    max-width: 700px;
    padding: 20px;
    margin: auto;
}

.form__container {
    margin: 20px 0px 20px 0px;
}

.form__button {
    padding: 5px;
    margin: 20px 0px 20px 0px;
    font-family: 'Raleway', sans-serif;
    max-width: 700px;
    width: 100%;
    border: none;
    border-radius: 5px;
    background-color: darkgoldenrod;
    color: #fff;
    transition: 0.7s;
    box-shadow: 0px 0px 19px 1px #1b1a1a;
    cursor: pointer;
}

.form__button:hover {
    background-color: #ddaa27;
}

.form__button:active {
    background-color: #e4b955;
}

.form__result {
    color: #e5e5e5;
}